:root {
  --column-count: 1;
}

html, h2, p {
  color: #060b35;
  margin: 0;
  font-family: Roboto, sans-serif;
  font-weight: 400;
}

body {
  margin: 0;
}

.header {
  text-transform: uppercase;
  justify-content: space-between;
  align-items: center;
  font-size: 12px;
  font-weight: 500;
  display: flex;
  position: relative;
  box-shadow: 2px 0 4px #0000000d;
}

.header__nav {
  margin-right: 50px;
}

.header__logo {
  margin: 0 50px;
}

.nav__list {
  gap: 20px;
  margin: 0;
  padding: 0;
  display: flex;
}

.nav__item {
  list-style: none;
  display: inline-block;
}

.nav__link {
  color: #060b35;
  align-items: center;
  height: 60px;
  text-decoration: none;
  transition: color .3s;
  display: flex;
  position: relative;
}

.nav__link:hover, .nav__link.is-active {
  color: #00acdc;
}

.nav__link.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.catalog {
  grid-template-columns: repeat(var(--column-count), 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .catalog {
    --column-count: 2;
  }
}

@media (width >= 768px) {
  .catalog {
    --column-count: 3;
  }
}

@media (width >= 1024px) {
  .catalog {
    --column-count: 4;
  }
}

.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  width: 200px;
  padding: 32px 16px 16px;
  transition: all .3s;
  display: flex;
}

.card:hover {
  transform: scale(1.2);
}

.card__image {
  margin-inline: auto;
  width: 160px;
  height: 134px;
  margin-bottom: 40px;
}

.card__title {
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
  transition: color .3s;
}

.card:hover .card__title {
  color: #34568b;
}

.card__code {
  color: #616070;
  margin-bottom: 16px;
  font-size: 10px;
  line-height: 14px;
}

.card__rewiew {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
  display: flex;
}

.card__rewiew-count {
  font-size: 10px;
  line-height: 14px;
}

.card__price {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
  display: flex;
}

.card__price-title {
  color: #616070;
  font-size: 12px;
  line-height: 18px;
}

.card__price-value {
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button-buy {
  box-sizing: border-box;
  color: #fff;
  text-transform: uppercase;
  cursor: pointer;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  transition: all .3s;
}

.card__button-buy:hover {
  color: #00acdc;
  background-color: #fff;
}

.stars {
  gap: 4px;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.stars--rating--1 > :nth-child(-n+1), .stars--rating--2 > :nth-child(-n+2), .stars--rating--3 > :nth-child(-n+3), .stars--rating--4 > :nth-child(-n+4), .stars--rating--5 > :nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}
